import React, { ChangeEvent, useEffect, useRef, useState } from "react";
import styles from "./index.less";
import { Tag, Input, Button } from "antd";
import { PlusOutlined, SearchOutlined } from "@ant-design/icons";

export interface ISearchInputProps {
  onSearch?: (conditions: string[]) => void;
}

const SearchInput: React.FC<ISearchInputProps> = (props) => {
  const { onSearch } = props;

  // 
  const [conditions, setConditions] = useState<string[]>([]);

  // 
  const [inputVisible, setInputVisible] = useState<boolean>(false);

  // 
  const [inputValue, setInputValue] = useState<string>("");

  // input ref
  const inputRef = useRef<Input>(null);

  // Tag
  const handleTagClose = (removedCondition: string) => {
    const newConditions = conditions.filter(
      (condition) => condition !== removedCondition
    );
    setConditions(newConditions);
  };
  // Tag
  const handleAddCondition = () => {
    setInputVisible(true);
  };

  useEffect(() => {
    if (inputVisible) {
      inputRef.current?.focus();
    }
  }, [inputVisible]);

  // Input
  const handleInputChange = (e: ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.target.value);
  };
  // 
  const handleInputConfirm = () => {
    const newConditions = [...conditions];
    if (inputValue && !conditions.includes(inputValue)) {
      newConditions.push(inputValue);
    }
    setConditions(newConditions);
    setInputVisible(false);
    setInputValue("");
  };

  // Search
  const handleSearch = () => {
    onSearch?.(conditions);
  };

  return (
    <div className={styles.searchContainer}>
      <div className={styles.search}>
        <div className={styles.searchLeft}>
          {conditions.map((condition: string, index: number) => {
            return (
              <span key={index} style={{ display: "inline-block" }}>
                <Tag
                  closable
                  onClose={(e) => {
                    e.preventDefault();
                    handleTagClose(condition);
                  }}
                >
                  {condition}
                </Tag>
              </span>
            );
          })}
          {inputVisible && (
            <Input
              ref={inputRef}
              type="text"
              size="small"
              style={{ width: 72 }}
              value={inputValue}
              onChange={handleInputChange}
              onBlur={handleInputConfirm}
              onPressEnter={handleInputConfirm}
            />
          )}
          {!inputVisible && (
            <Tag onClick={handleAddCondition}>
              <PlusOutlined /> New Condition
            </Tag>
          )}
        </div>
        <div className={styles.searchRight}>
          <Button icon={<SearchOutlined />} onClick={handleSearch} />
        </div>
      </div>
    </div>
  );
};

export default SearchInput;
